<template>
  <div class="container">
    <div class="row">
      <div class="col-12 col-xl-6 my-4 mx-auto" v-for="(item,index) in WorkData" :key="index">
        <div class="card bg-gray">
          <img class="card-img-top" :src="item.img"  alt="Card image cap" data-toggle="modal" data-target="#zoomImgModal" @click.prevent="zoom(item.img)">
          <div class="card-body">
            <p class="card-text">{{item.text}}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="modal fade bd-example-modal-xl" id="zoomImgModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-xl" role="document">
        <div class="modal-content">
          <div class="modal-boyd">
            <img id="zoomImg" class="img-fluid" :src="temp"  alt="Card image cap">
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data(){
    return{
      temp:''
    }
  },
  props:{
    WorkData:{
      type:Array,
      required:true
    }
  },
  methods:{
    zoom(data){
      this.temp = data
    }
  }

}
</script>