import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Store from '../views/AblumPage/Store.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path:'*',
    redirect:'/'
  },
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path:'/ablum/store',
    name: 'store',
    component: Store
  }
]

const router = new VueRouter({
  routes,
  linkActiveClass: 'active'
})

export default router
