<template>
  <div>


<nav class="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
  <span class="navbar-brand">我的履歷 Resume</span>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#myNavbar" aria-controls="myNavbar" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="myNavbar">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item">
        <a class="nav-link" @click.prevent="handler">基本資料</a>
      </li>

      <li class="nav-item">
        <a class="nav-link" @click.prevent="moveTo('#mySkill')">相關技能</a>
      </li>

      <li class="nav-item">
        <a class="nav-link" @click.prevent="moveTo('#myWork')">作品集</a>
      </li>

      <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" id="Myablum" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">圖文說明</a>
        <div class="dropdown-menu" aria-labelledby="Myablum">
          <div>
            <router-link class="dropdown-item" to="/ablum/store">電商網站 (後台登入)</router-link>
          </div>
        </div>
      </li>

    </ul>
  </div>
</nav>

  </div>
</template>

<script>
import $ from 'jquery';
export default {
  data(){
    return{}
  },
  methods:{
    handler(){
      if(this.$route.name !== 'Home'){
        this.$router.push('/')
        window.setTimeout(()=>{
          $('html, body').animate({scrollTop:0}, 1)
        },100)
      }
      else{
        $('html, body').animate({scrollTop:0}, 1)
      }
    },
    moveTo(id){
      if(this.$route.name !== 'Home'){
        this.$router.push('/')
      }
      window.setTimeout(()=>{
        $('html, body').animate({scrollTop: $(id).position().top}, 1)
      },100)
    }
  }
}
</script>

<style lang="scss" scoped>
a{
  cursor: pointer;
}
</style>