<template>
  <div>

    <div class="alert" :class="alertMessage.style" role="alert">
      <a :href="alertMessage.link" :class="alertMessage.textColor" target="_blank">
        <span v-if="alertMessage.date">{{alertMessage.date}}</span>
        {{alertMessage.message}}
      </a>
    </div>
      
  </div>
</template>

<script>
export default {
  data(){
    return{}
  },
  props:{
    alertMessage:{
      type:Object,
    }
  }
}
</script>